    },

    // 
    getAjaxResponse(obj, fn1, fn2) {
      var _this = this;
      var CancelToken = axios.CancelToken; //
      obj.cancelToken = new CancelToken(function executor(c) {
        _this.source = c;
      });
      axios(obj)
        .then(function(response) {
          fn1(response);
        })
        .catch(function(error) {
          fn2(error);
          // eslint-disable-next-line
          console.log(error);
        });
    },

