    dmFn() {
      var _this = this;
      clearTimeout(this.timer2);
      var para = {
        method: "get",
        url: _this.$api.GetRandomEnterpriseList
      };
      this.getAjaxResponse(
        para,
        function(successBack) {
          if (successBack.data.IsResult) {
            var word =
              successBack.data.Result[0].Title.length > 12
