<template>
  <!--  s -->
  <com-pop
    :my-index="'cert-ing'"
    class="certing"
  >
    <!-- @close="maskShow(item)" -->
    <template v-slot:pop-content>
      <!--  -->
      <div
        class="con-title"
        v-if="false"
      ></div>
      <div
        class="con-title"
        v-else
      ></div>
      <form class="con-content certing-content i-aut-config">
        <div class="con-group">
          <div class="con-name">:</div>
          <input
            class="con-val"
            name=""
            type="text"
            v-model="certingVal.name"
          />
        </div>
        <div class="con-group">
          <div class="con-name">:</div>
          <input
            class="con-val"
            name=""
            type="text"
            v-model="certingVal.code"
          />
        </div>
        <div class="con-group">
          <div class="con-name">:(500KB)</div>
          <!-- <input class="val" type="text" v-model="certingVal.code"/> -->
          <!--  s -->
          <div
            class="weui-cells weui-cells_form"
            id="uploader"
          >
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <div class="weui-uploader">
                  <div class="weui-uploader__hd">
                    <!-- <p class="weui-uploader__title"></p> -->
                    <div class="weui-uploader__info"><span id="uploadCount">{{certingBlob ? 1 :0}}</span>/1</div>
                  </div>
                  <div class="weui-uploader__bd">
                    <ul
                      class="weui-uploader__files"
                      id="uploaderFiles"
                    ></ul>
                    <div
                      class="weui-uploader__input-box"
                      ref="inputBox"
                    >
                      <input
                        ref="getFile"
                        @change="getPic"
                        id="uploaderInput"
                        class="weui-uploader__input"
                        name=""
                        type="file"
                        accept="image/jpeg,image/png,image/gif"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!--  e -->
        </div>
        <div class="con-group">
          <div class="con-name">:</div>
          <input
            class="con-val"
            name=""
            type="text"
            v-model="certingVal.linkman"
            placeholder=""
          />
        </div>
        <div class="con-group">
          <div class="con-name">:</div>
          <input
            class="con-val"
            name=""
            type="text"
            v-model="certingVal.linkphone"
            placeholder=""
          />
        </div>
        <div class="con-group">
          <div class="con-name">:<span class="cost">200/</span></div>
          <div class="des">1-2</div>
        </div>
      </form>
    </template>
    <template v-slot:flex-left>
    </template>
    <template v-slot:flex-right>
      <foot-btn>
        <div
          class="radiusBtn"
          @click="saveData"
        >
          
        </div>
      </foot-btn>
    </template>
  </com-pop>
  <!--  e -->

</template>

<script>
import ComPop from "../../../components/common/ComPop";
import FootBtn from "../../../components/common/FootBtn";

export default {
  components: {
    ComPop,
    FootBtn
  },

  data() {
    return {
      certingVal: {
        name: "",
        code: "",
        linkman: "",
        linkphone: ""
      },
      certingBlob: undefined
    };
  },

  methods: {
    saveData() {
      console.log(this.certingVal,this.certingBlob);
    },
    /**
     *  
     */
    picCheck() {
      // input file  
      let file = this.$refs.getFile.files,
        len = file.length;
      if (len > 0 && len < 2) {
        let size = file[0].size,
          fileSize = 1024 * 500, //  500kb 
          fileName = file[0].name, //
          fileNamearr = fileName.split("."),
          //
          suffix = fileNamearr[fileNamearr.length - 1],
          type = "jpg,jpeg,png,gif,JPG,JPEG,PNG,GIF",
          res = type.indexOf(suffix);

        if (res >= 0) {
          // 
          if (size > fileSize) {
            return " 500kb";
          } else if (size <= 0) {
            return "0";
          } else {
            return true;
          }
        } else {
          return " :" + type;
        }
      } else if (len >= 2) {
        return "";
      } else if (len == 0) {
        return "";
      }
    },

    getPic() {
      let res = this.picCheck();
      let file = this.$refs.getFile.files[0];
      console.log(file.type);
      
      if (res !== true) {
        alert(res);
      } else {
        let inputBox = this.$refs.inputBox;
        var reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = e => {
          let dataURL = e.target.result;
          //
          let url = `url(${dataURL})`;
          inputBox.style.backgroundImage = url;
          inputBox.classList.add("uploadBG");

          // picBlob 
          let picBlob = this.convertBase64UrlToBlob(dataURL,file.type);
          this.certingBlob = picBlob;
        };
      }
    },
    convertBase64UrlToBlob(urlData ,type) {
      var bytes = window.atob(urlData.split(",")[1]);
      var ab = new ArrayBuffer(bytes.length);
      var ia = new Uint8Array(ab);
      for (var i = 0; i < bytes.length; i++) {
        ia[i] = bytes.charCodeAt(i);
      }
      return new Blob([ab],{type : type});
    }
  }
};
</script>

<style lang="stylus" scoped>
.i-aut-config
  width 100%
  .uploadBG
    background-repeat no-repeat
    background-position center
    background-size contain
    &::after
      background-color transparent
    &::before
      background-color transparent
</style>
