<template>
  <div data-id="chart_Component">
    <!-- s-->
    <div
      class="chartLoading"
      v-show="ischartLoading"
    >
      <div class="chartLoadingImg">
        <img
          src="../../assets/icon/285.gif"
          class="w"
        />
      </div>
      <p class="mar_t_20 main_color_ffffff">...</p>
    </div>

    <!-- e-->

    <!--  s -->
    <transition>
      <!-- isShowMainSelect  -->
      <div
        class="index-mainSelect"
        v-show="isShowMainSelect"
      >
        <p
          class="index-mainSelect-close"
          @click="isShowMainSelect=false"
        >
          <i class="iconfont">&#xe603;</i>
        </p>
        <div class="index-mainSelect-title main_color_ffffff">
          
        </div>
        <div
          class="index-mainSelect-wrapper"
          ref="mainSelect"
        >
          <ul class="w">
            <!--  mainFnBtnList -->
            <li
              class="index-mainSelect-sec main_color_ffffff"
              v-for="(item) of mainFnBtnList"
              :key="item.id"
            >
              <div class="weui-cells_checkbox">
                <label :for="item.id">
                  <div class="mar_r_20">
                    <input
                      type="checkbox"
                      class="weui-check"
                      name="checkbox1"
                      :id="item.id"
                      v-model="item.isCheck"
                      :disabled="item.id==1"
                    >
                    <i
                      class="iconfont"
                      :style="{'color':item.isCheck?'#e3000b':'#fff'}"
                    >{{item.isCheck?'&#xe632;': '&#xe681;' }}</i>
                  </div>
                  <div>{{item.title}}</div>
                </label>
              </div>
            </li>
          </ul>
        </div>
        <div class="index-serNote-con-btn">
          <!---  --->
          <a
            class="dis_bl main_bgcolor_ff8800 main_color_ffffff"
            style="width:100%"
            @click="saveMainFn"
          ></a>
        </div>
      </div>
    </transition>
    <!--  e -->

    <template v-if="this.$store.state.loginInfo.showingCId != ''">
      <!--  s-->
      <base-info
        :industryid='industryid'
        :cID='cID'
      ></base-info>
      <!--  e-->

      <!--  s-->
      <shareholder-info></shareholder-info>
      <!--  e-->

      <!--  s-->
      <key-person></key-person>
      <!--  e-->

      <!--  s -->
      <!-- <foreign-investment></foreign-investment> -->
      <!--  e -->

      <!--  s -->
      <set-enterprise-tips></set-enterprise-tips>
      <!--  e -->

      <!--  s -->
      <set-enterprise></set-enterprise>
      <!--  e -->
    </template>
  </div>
</template>

<script>
import Vue from "vue";
import * as d3 from "d3";
import axios from "axios";
import BScroll from "better-scroll";
// 
const BaseInfo = resolve => {
  import("@/components/shangLian/dimenPop/BaseInfo").then(module => {
    resolve(module);
  });
};
// 
const ShareholderInfo = resolve => {
  import("@/components/shangLian/dimenPop/ShareholderInfo").then(module => {
    resolve(module);
  });
};
// 
const KeyPerson = resolve => {
  import("@/components/shangLian/dimenPop/KeyPerson").then(module => {
    resolve(module);
  });
};
// 
const ForeignInvestment = resolve => {
  import("@/components/shangLian/dimenPop/ForeignInvestment").then(module => {
    resolve(module);
  });
};
// 
const SetEnterpriseTips = resolve => {
  import("@/components/pop/certification/setEnterpriseTips").then(module => {
    resolve(module);
  });
};

// 
const SetEnterprise = resolve => {
  import("@/components/pop/certification/setEnterprise").then(module => {
    resolve(module);
  });
};

// import ComPop from '@/components/common/ComPop'
// import commonFn from "@/assets/js/commonFn";
// let baseurl = "";
var funTarget = null;
let vm = null;

/**
 *  getM 
 *   #$weiduClick s
 * gw.on("click", function () {
 * d3.select(this).classed("btnActive", function () {
 *   e
 *
 *  getMainFnBtn
 *   refreshInfo
 *   checkDimensionInfo
 *   refreshInfo
 *  checkDimensionInfo
 * 
 *
 * getMDOM #$DOMdata
 */
export default {
  components: {
    BaseInfo, //
    ShareholderInfo, //
    KeyPerson, //
    ForeignInvestment, //
    SetEnterpriseTips, //
    SetEnterprise //
  },

  props: {
    // 
    isApprove: Boolean,
    id: {
      type: String,
      default: ""
    },
    userInfo: {
      // 
      type: Object,
      default: {}
    }
  },

  data() {
    return {
      //id
      cID: "", //id
      streamData: {
        upData: {
          data: [], //
          total: 0 //
        },
        downData: {
          data: [], //
          total: 0 //
        },
        title: "", //
        visit: 0, //
        rec: 0 //
      },
      //
      dataCheckdimension: [
        { slice: 0, segment: 1, value: 20, color: "#3A6E86", title: "" },
        { slice: 0, segment: 2, value: 5, color: "#3A6E86" },
        { slice: 0, segment: 3, value: 10, color: "" }
      ],

      // 
      streamData2: {
        nodes: [
          {
            id: "main", //ID
            name: "main", //
            data: [] //
            /**
             * @data
             * {
             *   color: "#3A6E86"
             *   offset: 0
             *   segment: 1
             *   slice: 0
             *   title: ""
             *   value: 20
             * }
             */
          }
        ],
        edges: [],
        companyName: "", //
        visitNum: "", //
        recNum: "" //
      },
      newUser: false,
      userInfoChart: null,
      saveInfoList: null, //  
      //list
      mainFnBtnList: [
        { title: "", isCheck: false, id: "", color: "", en: "", icon: "" }
      ],

      //
      isShowMainSelect: false, //
      ischartLoading: false, // loading 

      // 
      isClick: false,
      // !warn ID1 index+1
      industryList: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      tdata: [], //
      tCompany: {}, //
      industryid: "" //id,
    };
  },

  watch: {
    "userInfoChart.chbIsDefaultPage": {
      handler(newName, oldName) {
        if (d3.select("#certification")) {
          d3.select("#certification").classed("dis_none", function(d) {
            if (newName) {
              return false;
            }
            return true;
          });
        }

        //
      },
      immediate: true,
      deep: true
    },

    // ?cID(ID)
    // *cID  
    // cID
    //    

    "$store.state.loginInfo.showingCId": {
      handler: function(newCId) {
        if (newCId != "") {
          // console.log(newCId,'newCIdnewCIdnewCIdnewCIdnewCIdnewCIdnewCId');
          // !
          function getCheckdimension() {
            return vm.$axios.post(vm.$api.GetCheckdimension, {
              // params:{
              //   id:commonFn.handleStroage.getLocalStorage("userInfo").ID || vm.userInfoChart.ID,
              pageIndex: 1,
              pageSize: 30,
              where: [
                {
                  name: "userid",
                  value: vm.$store.state.loginInfo.userInfo.ID
                  // vm.$commonFn.handleStroage.getLocalStorage("userInfo").ID,
                }
              ]
              // }
            });
          }

          // !
          function SelectDimDetial() {
            return vm.$axios.post(vm.$api.SelectDimDetial, {
              // params:{
              //   id:commonFn.handleStroage.getLocalStorage("userInfo").ID || vm.userInfoChart.ID,
              pageIndex: 1,
              pageSize: 30,
              where: [
                {
                  name: "id",
                  value: 1
                },
                {
                  name: "enterpriseid", //ID
                  value: newCId // ID
                }
              ]
              // }
            });
          }

          // ! 
          function GetFirstSave() {
            return vm.$axios.get(vm.$api.GetFirstSave, {
              params: {
                //   id:commonFn.handleStroage.getLocalStorage("userInfo").ID || vm.userInfoChart.ID,
                userId: vm.$store.state.loginInfo.userInfo.ID
                // vm.$commonFn.handleStroage.getLocalStorage("userInfo").ID
              }
            });
          }

          //  
          // !warn  
          function loginFresh() {
            getCheckdimension()
              .then(checkState => {
                let checkStateRes = checkState.data;
                let DimensionList; //
                let transCheckState = []; //

                // 
                let iconList = [
                  "&#xe64c;",
                  "&#xe66b;",
                  "&#xe615;",
                  "&#xe643;",
                  "&#xe652;",
                  "&#xe64b;",
                  "&#xe664;",
                  "&#xe607;",
                  "&#xe637;",
                  "&#xe6e7;",
                  "&#xe663;",
                  "&#xe608;",
                  "&#xe65d;",
                  "&#xe677;"
                ];

                // 
                let colorList = [
                  "#cfbb0e",
                  "#ecce1e",
                  "#e09d10",
                  "#b94514",
                  "#a52c19",
                  "#7a494c",
                  "#515868",
                  "#36748b",
                  "#85a157",
                  "#bfbf07",
                  "#5359A6",
                  "#FF8800",
                  "#61A0A8",
                  "#A0E13A"
                ];
                // 

                let rows = checkStateRes.Result.rows; //

                if (rows != null) {
                  // 
                  vm.newUser = false;

                  let listinfo = rows.listinfo; //

                  if (checkStateRes.IsResult) {
                    let saveInfoList = {
                      hidSxUserID: vm.$store.state.loginInfo.userInfo.ID
                      // vm.$commonFn.handleStroage.getLocalStorage("userInfo").ID
                    };

                    //  
                    Object.keys(listinfo).forEach((keyItem, index) => {
                      let title = listinfo[keyItem][0].title; //

                      let titleid = listinfo[keyItem][0].titleid; //ID

                      let stateName = Object.keys(
                        listinfo[keyItem][0].state
                      )[0]; //state

                      let state = listinfo[keyItem][0].state[stateName]; //state 

                      //   true
                      if (titleid == 1) {
                        state = true;
                      }

                      //   saveInfoList 
                      saveInfoList[stateName] = state;

                      let colorItem = colorList[index];
                      let iconItem = iconList[index];

                      // 
                      if (
                        colorList[index] == undefined ||
                        iconList[index] == undefined
                      ) {
                        colorItem = colorList[index % colorList.length];
                        iconItem = iconList[index % iconList.length];
                      }

                      let checkStateItem = {
                        title, //
                        id: titleid, //id
                        isCheck: state, //
                        stateName, //
                        color: colorItem, //
                        icon: iconItem //
                      };

                      transCheckState.push(checkStateItem);
                    });

                    vm.$set(vm, "saveInfoList", saveInfoList);
                  }

                  vm.$set(vm, "mainFnBtnList", transCheckState);

                  vm.ischartLoading = true;
                  //
                  let funTarget = null;
                  let width = 1124,
                    height = 2000;

                  //    
                  //t.dragForce(_this.streamData);
                  vm.getMainFnBtn(width, height, newCId);
                  // vm.saveMainFn(); //

                  // checkDimensionInfo  
                  //  getMainFnBtn() 
                  //  s
                  // vm.refreshInfo()  // localStorage
                  // vm.checkDimensionInfo(cID) //

                  // vm.getEnterpriseList(vm.cID);  //cID(id) 
                  //  e
                } else {
                  vm.newUser = true;
                  //  null 

                  // ! 
                  GetFirstSave()
                    .then(res => {
                      let { IsResult, Result } = res.data;
                      if (IsResult && Result === null) {
                        vm.newUser = false;

                        loginFresh();
                      } else {
                        return "err";
                      }
                    })
                    .catch(rej => console.log(rej, "GetFirstSave rej"));
                }
              })
              .catch(rej => console.log(rej, "getCheckdimension rej"));
          }

          loginFresh();
        }
      },
      deep: true,
      immediate: true
    }
  },
  beforeCreate() {
    vm = this;
    //

    let isMock = true;
    this.$axios.get(`http://192.168.31.226:8000/index/info/`).then(res => {
      this.tCompany = res;

      console.log("===============", this.tCompany);
    });

    //;
    this.$axios.get("http://192.168.31.226:8000/index/industry").then(res => {
      this.tdata = res;
    });
  },
  beforeMount() {
    vm.userInfoChart = vm.userInfo; // userInfoChart
    vm.getEnterpriseList(); //#$1 
    /**
     * id 
     *   
     * 
     */
  },

  mounted() {
    //   item
    var mainSelect = vm.$refs.mainSelect;

    vm.$root.bus.$on("changesearchlist", function(id) {
      // SearchBoxItem  #$chart
      vm.$store.commit("showingCId", id);
      vm.getEnterpriseList(id); //
    });

    if (!this.scroll) {
      this.scroll = new BScroll(mainSelect, {
        click: true,
        scrollY: true,
        probeType: 3
      });
    } else {
      this.scroll.refresh();
    }

    //
    // this.$axios.get("http://192.168.31.226:8000/index/industry?enterpriseid=0be46c589062385fa8094eb57dd0afa1&industryid=4").then(res => {
    //   this.tdata = res;
    // });
  },

  methods: {
    // ajax 
    getAjaxResponse(obj, fn1, fn2) {
      var _this = this;
      var CancelToken = axios.CancelToken;
      obj.cancelToken = new CancelToken(function executor(c) {
        vm.source = c;
      });
      axios(obj)
        .then(function(response) {
          fn1(response);
        })
        .catch(function(error) {
          fn2(error);
          console.log(error);
        });
    },

    //  getM
    //ID 

    // !warn getPlus   
    // *0 false 1 true  gw(getPlus  0 1 )
    //&&
    // !warn  
    getPlus(_this, d, tag) {
      /**
       * @params
       * _this: DOM
       * d    : DMOdata 
       * tag  : 
       * _this data DOM
       */
      // #$ 
      var point = d.data.point;
      /**
              _this   <g class="title btnActive">
             */
      /**

      /**
       * 
       * 
       */
      if (_this.className.baseVal.split(" ").length > 1) {
        return 0;
        // *0  1 
      } else {
        d3.select(_this.parentNode)
          .selectAll(tag)
          .filter(function(data, index) {
            // q   i 
            // d
            if (data.index != d.index) {
              //      btnActive
              d3.select(this).classed("btnActive", false);
            }
          });
        var url = "",
          Where = null;
        // 
        if (point == "up") {
          url = this.$api.GetUpPreciseList;
          Where = [
            //{
            //"name": "enterpriseid",
            //"value": point == "up" ? d.data.cId : ""
            //},
            //{
            //    "name": "classid",
            //    "value": d.data.insId
            //},
            {
              name: "provinceid",
              value: d.data.pId
            }
          ];
        } else if (point == "down") {
          url = this.$api.GetDownPreciseList;
          Where = [
            {
              name: "classid",
              value: d.data.insId
            },
            {
              name: "provinceid",
              value: d.data.pId
            }
          ];
        } else {
          // point
          // 
          return 1;
          // *0  1 
        }
        //
        var para = {
          method: "post",
          url: url,
          data: {
            PageIndex: 1,
            PageSize: 5,
            Where: Where
          }
        };

        this.getAjaxResponse(
          para,
          function(successBack) {
            if (successBack.data.IsResult) {
              // console.log(successBack,'getPlus successBack');
            }
          },
          function(errorBack) {
            console.log(errorBack);
          }
        );
        //this.isShowSearchBox = true
        return 1;
      }
    },

    // 
    setIndex() {
      this.$store.commit("showPop", {
        popName: "tips",
        config: {
          text: ` ${this.streamData.title} ?`,
          confirm() {},
          cancel() {
            // alert('haha')
          }
        }
      });
    },

    showNumFix(data) {
      data = (data / 10000).toFixed(2);
      if (data < 0.01) return 0.01 + "";
      return data + "";
    },

    //
    getMainFnBtn(width, height, id) {
      /**
       * @params
       * !   ID
       * */

      d3.select("#svgg")
        .selectAll("*")
        .remove(); //
      let svgW = d3
          .select("#svgg")
          .attr("viewBox", "0 0 1124 2000")
          .attr("preserveAspectRatio", "xMidYMid meet"),
        svgDom = svgW.node(),
        svgWidth = svgDom.clientWidth,
        svgHeight = svgDom.clientHeight,
        heightRatio = height / svgHeight, // svg
        widthRatio = width / svgWidth; // svg
      //  s

      vm.refreshInfo(); // localStorage
      this.checkDimensionInfo(id); //*, ID
      vm.$set(this.streamData2, "nodes", this.streamData2.nodes.slice(0, 1));

      this.streamData.upData.data.forEach(dateItem => {
        dateItem.Province.forEach(PItem => {
          // console.log(PItem,"PItem PItem PItem test")
          PItem.valueOfTag =
            PItem.value == 0 || dateItem.proTotal == 0
              ? 0
              : ((PItem.Count / dateItem.proTotal) * 100).toFixed(2) + "%";
        });
      });

      var nodes = this.streamData2.nodes.concat(this.streamData.upData.data);

      vm.$set(this.streamData2, "nodes", nodes);
      //  this.streamData2.nodes = this.streamData2.nodes.concat(streamData.upData.data);

      ////
      this.streamData.downData.data.forEach(downItem => {
        downItem.Province.forEach(PItem => {
          PItem.valueOfTag =
            PItem.value == 0 || downItem.proTotal == 0
              ? 0
              : ((PItem.Count / downItem.proTotal) * 100).toFixed(2) + "%";
        });
      });
      var node2 = this.streamData2.nodes.concat(this.streamData.downData.data);
      vm.$set(this.streamData2, "nodes", node2);
      ////
      var t_a = [];
      for (var i = 0; i < this.streamData2.nodes.length; i++) {
        if (this.streamData2.nodes.length > 1 && i > 0) {
          var o = {};
          o.target = "main";
          o.source = this.streamData2.nodes[i].id;
          t_a.push(o);
        }
      }
      vm.$set(this.streamData2, "edges", t_a); //edges 

      (this.streamData2.companyName = this.streamData.title),
        (this.streamData2.visitNum = this.streamData.visit),
        (this.streamData2.recNum = this.streamData.rec);

      // e

      //d3alphaTarget()
      /*
            alphaTargetalphaMin)
            alphaTarget0
            */

      //icon
      let Font = 20;
      //

      // alpha .
      var simulation = d3
        .forceSimulation()
        .force(
          "link",
          d3
            .forceLink()
            .distance(220)
            .id(function(d) {
              return d.id;
            })
        ) // distance
        .force(
          "collide",
          d3.forceCollide().radius(d => {
            // forceCollide 
            //radius:
            return 250;
          })
        ) // collide radius 
        .force("charge", d3.forceManyBody().strength(-100))
        // forceManyBody 
        // charge   
        //  .strength
        .force("center", d3.forceCenter(width / 2, height / 2));
      //  xy  x  y 0,0

      var svg = svgW.append("g"); //.attr("width","100%").attr("height","100%");
      var link = svg
        .append("g") // link 
        .attr("class", "links")
        .selectAll("line")
        .data(this.streamData2.edges) //data
        .enter()
        .append("line")
        .attr("stroke", "#fff")
        .attr("stroke-opacity", "0.8");
      var node = svg
        .append("g") //
        .attr("class", "nodes")
        .selectAll("g")
        .data(this.streamData2.nodes) //data
        .enter()
        .append("g")
        .attr("class", "secBtn")
        .property("active", false);

      //d3.behavior.drag()call()
      //
      node.call(
        d3
          .drag()
          .on("start", dragstarted)
          .on("drag", dragged)
          .on("end", dragended)
      );

      //   
      node
        .append("rect")
        .attr("class", "dis_none")
        .attr("width", "1124")
        .attr("height", "2000")
        .attr("x", -562)
        .attr("y", -1000);

      simulation
        .nodes(this.streamData2.nodes) //
        .on("tick", ticked); //
      simulation
        .force("link") //
        .links(this.streamData2.edges);
      // remove  
      node
        .exit()
        .transition()
        .remove();
      link
        .exit()
        .transition()
        .remove();
      //
      //
      let lastClickObj = null;

      function dragstarted(d) {
        //  : "active" 
        if (d3.select(this).property("active")) {
          return;
        }
        funTarget = "drag1";
        // 
        if (!d3.event.active) simulation.alphaTarget(0.1).restart();
        d.fx = d.x;
        d.fy = d.y;
      }

      //   d3.event 
      function dragged(d) {
        funTarget = "drag2";
        d.fx = d3.event.x;
        d.fy = d3.event.y;
      }

      let timer = null;
      let t = d3
        .transition()
        .duration(400)
        .ease(d3.easeLinear);

      function dragended(d) {
        if (d3.select(this).property("active")) return; //
        simulation.restart();
        var name = d.name;
        //==
        // funTarget  dragstarted  drag1 
        // dragged funTarget  drag1
        if (funTarget == "drag1") {
          if (!d3.select(this).property("active")) {
            d.fx = null;
            d.fy = null;
            if (!timer) {
              timer = setTimeout(() => {
                var nodes = d3.select(this);
                // 
                // nodes.transition(t) 
                nodes.transition(t).attr("transform", function(q) {
                  var x,
                    y,
                    r,
                    m = 1.5,
                    nx,
                    ny;
                  //;
                  if (q.id == "main") {
                    // #$dcw
                    r = document.documentElement.offsetWidth / 2;
                  } else {
                    r = document.documentElement.offsetWidth / 4;
                  }
                  nx = 1124 - r - 100 * m;
                  ny = 2000 - r - 100 * m;
                  if (q.x < r * m + 15 * m) {
                    x = r * m + 15 * m;
                  } else if (q.x > nx) {
                    x = nx;
                  } else {
                    x = q.x;
                  }
                  if (q.y < r * m + 40 * m) {
                    y = r * m + 40 * m;
                  } else if (q.y > ny) {
                    y = ny;
                  } else {
                    y = q.y;
                  }

                  //  
                  vm.isClick = true;
                  return "translate(" + 562 + "," + 1000 + ") scale(" + m + ")";
                });

                nodes.property("active", true);
                nodes.select("rect").classed("dis_none", false);
                //
                this.parentNode.appendChild(this);
                d3.selectAll(".secBtn").filter(function(z, i) {
                  if (z.name != name) {
                    d3.select(this).property("active", false);
                    d3.select(this)
                      .select("rect")
                      .classed("dis_none", true);
                  }
                });
                simulation.stop();

                clearTimeout(timer);
                timer = null;
              }, 100);
            }
            // ! 
            // 
            // 
