      //   
      function ticked() {
        // 
        node.attr("transform", function(d, i) {
          /**
           *  d.x d.y xy
           */

          let x = d.x, //viewBoxx
            y = d.y, //viewBoxy
            dWidth = d.width, //
            dHeight = d.height, //
            viewWidth = width, // viewBox
            viewHeight = height, // viewBox
            rx = (dWidth / 2) * heightRatio,
            ry = (dHeight / 2) * widthRatio;

          /** 50  */
          if (x < rx) x = rx - 50; //
          if (x > viewWidth - rx) x = viewWidth - rx + 50; //
          if (y < ry) y = ry; //
          if (y > viewHeight - ry) y = viewHeight - ry; //
          // if(d.name=='main')console.log('main d ' ,d);
          d.x = x;
          d.y = y;
          return "translate(" + x + "," + y + ") scale(1)";
        });

        // 
        link
          .attr("x1", function(d) {
            return d.source.x;
          })
          .attr("y1", function(d) {
            return d.source.y;
          })
          .attr("x2", function(d) {
            return d.target.x;
          })
          .attr("y2", function(d) {
            return d.target.y;
          });
