</template>

<script>
let vm = null,
  d3 = null;
import SearchCompany from "@/components/pop/lianQuan/SearchCompany";
import CompanyList from "@/components/pop/shanglian/CompanyList"; // 
import CompanySetting from "@/components/pop/companySetting/CompanySetting";
import CompanyContact from "@/components/pop/companySetting/CompanyContact";
import TemplateList from "@/components/pop/pushTemplate/TemplateList";
import SelectTemType from "@/components/pop/pushTemplate/selectTemType";
import MsgTemplate from "@/components/pop/pushTemplate/MsgTemplate";
import EditMsgTemplate from "@/components/pop/pushTemplate/EditMsgTemplate";
import VideoTemplate from "@/components/pop/pushTemplate/VideoTemplate";
import SelfInfoPop from "@/components/pop/selfPic/InfoPop";
import ManageBtn from "@/components/pop/manageBtn/ManageBtn";
import GroupManage from "@/components/pop/manageBtn/GroupManage";
import { stringify } from 'querystring';

export default {
  name: "lianQuan",
  components: {
    SearchCompany,
    CompanyList,
    CompanySetting,
    CompanyContact,
    TemplateList,
    MsgTemplate,
    EditMsgTemplate,
    VideoTemplate,
    SelectTemType,
    SelfInfoPop,
    ManageBtn,
    GroupManage
  },

  data() {
    return {
      json: {},
      d3Obj: {},
      svgWidth:0, //svg
      svgHeight:0, //svg,
      baseSide:0, //svgWidth svgHeight 
      isPlus:false, // 
      canDragged:true, // 
      bgShade:null, //d3 
      
      // sizeSet s
      textFontSize:16, //
      pieNodeRadius:50,//
      outerRadius:60, // 
      borderRadius:60/3, //
      nodePadding:220, //

      // sizeSet e

      // mainBtn s
      mainBtnscale:1.4, //
      mainBtnDiameter:0, //
      // mainBtn e

      // botInfoSide s 
      blackSideHeight:0, //
      leftWidth:0, //   
      rightWidth:0, //   
      // botInfoSide e

      // ajax get data s
      upJson:null, //
      downJson:null, //
      // show data e
    };
  },
  beforeCreate() {
    vm = this;
    d3 = this.$d3;
  },
  async created() {
    
    let json = await this.$ajax.getLian(); //
    this.json = json; 
    // 
      let graphData = this.dataDeal({
        forceData: json //
    });

    // 
    let downData = await this.$axios.get(this.$api.downData).then(res=>res.data)
    .catch(rej=>{console.log(rej)})

    let upData = await this.$axios.get(this.$api.downData).then(res=>res.data)
    .catch(rej=>{console.log(rej)})

    let newJson = this.json2forceData(downData,upData);

    this.$nextTick()
        .then(this.sizeSet())//
        .then(this.colorIcon())
        .then(this.initAtlasPie()) //
        .then(this.init(newJson)) // 
        .then(this.initBotInfoPie()) // 
        .then(this.initBotInfoSide()) // 
    // this.$store.commit('showPop','editTemplate');

  },
  methods: {
    json2forceData(downJson,upJson){
      // dataType 
      //  down up 
      let forceData={};
      forceData.links = [];
      forceData.nodes = [];
      let links = forceData.links,
          nodes = forceData.nodes;      

      this.downJson = downJson; //
      this.upJson = upJson; //

      let showUp = JSON.parse(JSON.stringify(upJson)) //  
      let showDown = JSON.parse(JSON.stringify(downJson)) //  

      //  
      let upForceData = this.getPieNodeForceData(forceData,showUp,'up') // 
      let downForceData = this.getPieNodeForceData(forceData,showDown,'down') //

      forceData.links=[...upForceData.links,...downForceData.links]
      forceData.nodes=[...upForceData.nodes,...downForceData.nodes]
      forceData.nodes.push({
        id:'main', // 
        pieType:'main'
      })

      return forceData
    },
    // 
    merge2Details(arr){
      let arrTem = [];
      arrTem = arr.splice(15,arr.length-10);
      let Count = 0;
      arrTem.forEach((ele)=>{
        Count += +ele.Count; //
      })
      arr.push({
        ID:'other',
        Name:'',
        Count
      })
    },
    // d3
    getPieNodeForceData(forceData,json,dataType){
        let links = [];
        let nodes = [];

        json.forEach((ele)=>{
          // 
          //  
          this.$commonFn.dataDesSort(ele.Province,'Count')
          this.merge2Details(ele.Province)
        })

        json.forEach(ele=>{
          links.push({
            source:ele.ClassName.Name+dataType, // 
            target:'main' //  
          })
        })

        // 
        json.forEach((ele,index)=>{
          ele.Province.forEach(item=>{})
          nodes.push({
            pieType:'pie',
            id:ele.ClassName.Name+dataType, //ID ID  ID
            industryName:ele.ClassName.Name,
            industryID:ele.ClassName.ID, //ID
            provinceList:ele.Province, // 
            dataType,
          })
        })

        return {
          links,
          nodes
        }
    },

    //  
    dataDeal({ forceData = {} }) {
      let graphData = forceData; //
      // ...
      //  
      Array.prototype.forEach.call(graphData.nodes, ele=>{
        
        let eleId = ele.id,
        pieType = ''
        
        switch(eleId.slice(0,3)){
          case 'pie' :
            pieType = 'pie'
          break;
          case 'add' :
            pieType = 'add'
          break;
          default:
            pieType = 'main'
        }
        
        ele.pieType = pieType
      })
      return graphData;
    },
    // 
    sizeSet(){
      let screenWidth = document.documentElement.offsetWidth, //
          screenHeight = document.documentElement.offsetHeight, //
          baseSide = 0
      ;
      let svg = d3.select("svg"),
       svgWidth = +svg.node().clientWidth,
       svgHeight = +svg.node().clientHeight;
       this.svgWidth = svgWidth;
       this.svgHeight = svgHeight;

      //  ! (/)
      let pieNodeDiameter  =  Math.sqrt((this.svgWidth*this.svgHeight/28));

      if(screenWidth > screenHeight ){
        baseSide = screenHeight
      }else{
        baseSide = screenWidth
      }
      //  
      this.baseSide = baseSide
      // pie/2
      this.pieNodeRadius = pieNodeDiameter/2

      // mainBtn 
      this.outerRadius = (baseSide/4/2);
      this.borderRadius = (this.outerRadius /5);

      // 
      this.nodePadding = (vm.outerRadius + vm.borderRadius)*4;

      // pie
      this.textFontSize = this.pieNodeRadius/4;
      

    },
    //  
    initAtlasPie(){
      let svg = d3.select("svg"),
      pieSymbol, // Symbol
      pieG, //
      outerRadius = this.outerRadius, //
      colorList =['#61a0a8','#ff8900','#d48265'],
      datas = [
        {title:'t1',value:1},
        {title:'t2',value:1},
        {title:'t3',value:1},
      ],
      pie = d3.pie().value(item=>item.value), //
      pieRes = pie(datas),
      arc = d3.arc().innerRadius(0).outerRadius(outerRadius - 2) //
      ;


      // symbol 
      pieSymbol = svg
                    .append('symbol')
                    .attr('id','pie')
                    .attr('viewBox',`0 0 ${outerRadius * 2} ${outerRadius * 2}`);
      // G 
      pieG = pieSymbol.append('g')
      
      pieRes.forEach((pieData,index)=>{
        let arcRes = arc(pieData);

        let piePath  = pieG
                        .append('path')
                        .attr('d',arcRes)
                        .attr('stroke','transparent')
                        .attr('fill',colorList[index])
                        
      });
      pieG.append('circle')
          .attr('cx',0)
          .attr('cy',0)
          .attr('r',outerRadius/2)
          .style('fill','black') 

      pieG.attr('transform', `translate(${outerRadius}, ${outerRadius}) rotate(90)`);

    },
    //  pie
    initPie(nodePie){
      let pie = d3.pie().value(item=>item.Count),//
          arc = d3.arc().innerRadius(0).outerRadius(outerRadius - 2), //
      outerRadius = this.outerRadius, //
      colorList =['#61a0a8','#ff8900','#d48265'],
      pieData=[] //
      ; 

      nodePie.datum(function(d){
        pieData.push(pie(d.provinceList)); //   
      })

      nodePie.append('g')
             .append('path')
             .attr('d',pieData)
      pieData.forEach((ele,index)=>{
          
        let arcRes = arc(ele);
        let piePath  = nodePie
                        .append('path')
                        .attr('d',arcRes)
                        .attr('stroke','transparent')
                        .attr('fill',function(d,index){
                          if(index < colorList.length){
                            return colorList[index]
                          }else{
                            return colorList[0]
                          }
                        })
        })
    },
    
    //  
    colorIcon(){
      let svg = d3.select('svg'),
      defs = svg.append('defs'),
      gradient = defs.append('linearGradient'), //
      stopColor = gradient.append('stop'),
      stopColor2 = gradient.append('stop')
      ; //

      gradient.attr('id','main-color')
              .attr("x1", "0%")
              .attr("y1", "0%")
              .attr("x2", "0%")
              .attr("y2", "100%");

      stopColor.attr("offset", "0%")
               .attr("style", "stop-color:rgb(241,241,243);stop-opacity:1");
      stopColor2.attr("offset", "100%")
                .attr("style", "stop-color:rgb(178,178,178);stop-opacity:1");

    },
    
    //  
    init(graphData) {
      let svg = d3.select("svg"),
       svgWidth = this.svgWidth, //svg
       svgHeight = this.svgHeight; //svg

      this.graphData = graphData // ajax

       // 

      // simulation 
      let simulation = this.initSimulation(),

      // 
       link = this.initLinks(graphData),
       // 
       getNodes = this.initNodes(graphData,simulation), //selection
       {nodeG,industryNodeG,mainNodeG}= getNodes
       ;
       this.eventManage(getNodes,simulation,graphData,link,industryNodeG,mainNodeG,svgWidth,svgHeight); //
      
      // link nodex,y
      // simulation.nodes(graphData.nodes).on("tick", ()=>vm.ticked(link,nodeG,svgWidth,svgHeight));
      // links
      simulation.force("link").links(graphData.links);

    },
    
    // 
    initSimulation() {
      // simulation.
      let simulation = d3
        .forceSimulation()
        .alphaMin(0.01)
        .force(
          "link",
          d3
            .forceLink()
            .distance(vm.nodePadding) //
            .id(function(d) {
              return d.id;
            })
        )
        //
        .force("collide",d3.forceCollide().radius(d => vm.outerRadius+vm.borderRadius) )
        // forceManyBody 
        // charge   
        .force("charge", d3.forceManyBody())
        //
        .force("center", d3.forceCenter(vm.svgWidth / 2, vm.svgHeight / 2)); 

        return simulation
    },

    // links 
    initLinks(graphData) {
      let svg = d3.select('svg');
      let link = svg.append("g")
        .attr("class", "links")
        .selectAll("line")
        .data(graphData.links)
        .enter()
        .append("line")
        .attr('stroke','white')

      return link;
    },
    // nodes 
    initNodes(graphData,simulation) {

      let svg = d3.select('svg'), //svg
      nodes,// nodeG industryPie
      nodeG, // nodesg   industryPie nodeText
      industryNodeG,//
      mainNodeG,//
      industryPie,//use
      nodeText, //
      textFontSize = this.textFontSize,  //text
      mainBtnUse,//use
      addBtnUse,//use
      pieBtnUse,//use
      // arc = d3.arc(), // d3arc
      pieNodeRadius = this.pieNodeRadius, //
      outerRadius = this.outerRadius, // 
      borderRadius = this.borderRadius //
      ;

      // 
      nodes = svg.append("g")
                .attr("class", "nodes")
                .selectAll("circle")
                .data(graphData.nodes) // 
                .enter()
                .datum(function(d,i) {
                // 
                  switch(d.pieType){
                    case 'main':
                      d.radius = outerRadius+borderRadius;
                    break;
                  }
                  d.nodeOrder = `node-${i}`;
                  return d;
                })
      //  
      nodeG = nodes.append('g')
      nodeG.attr('class',function(d){
        if(d.pieType=='pie'){
          return 'industry'
        }
        else{
          return 'main'
        }
      })
      .attr('id',function(d,i){
      //  node 
        return `node-${i}`;
      })
      .attr('data-industry',function(d){
        return d.id
      })
      .attr('data-industry-id',function(d){
        return d.industryID
      })
           
      
      // add & pie
      industryNodeG = nodeG.select(function(d){
        if(d.pieType != 'main') {
          return this;
        }
      })
      // mainBtn
      mainNodeG =  nodeG.select(function(d){
        if(d.pieType == 'main') return this
      })
      this.initMainBtn(mainNodeG,outerRadius,borderRadius) //

      industryPie = industryNodeG.append("g")
            .attr('class','industry-pie')
            .attr("width", d=>d.radius*2)
            .attr("height", d=>d.radius*2)
            // .attr('transform',d=>`translate(${-d.radius},${-d.radius})`)

    // ====================================================================================
      this.initPieNode(industryPie) //

    // ====================================================================================

      // text
      
      nodeText = nodeG.append('text')
      
      nodeText.style('font-size',textFontSize)
              .attr('class','node-name')
              .attr('fill','white')
              .attr('text-anchor','middle')
              .attr('transform',function(d){
                return `translate(0,${d.radius + textFontSize})`
              })
              .html(function(d){
                if(d.pieType == 'pie'){
                  return d.industryName
                }else{
                  return
                }
              })
              .style('font-family','TRENDS')

      //   
      // addBtnUse = industryPie
      //             .select(function(d){
      //               if(d.pieType == 'add') return this
      //             })
      // addBtnUse.attr("xlink:href", "#add-icon")
      //          .attr('class','add-btn')

      //   
      // pieBtnUse = industryPie
      //             .select(function(d){
      //               if(d.pieType == 'pie') return this
      //             })
      // pieBtnUse.attr("xlink:href", "#pie")
      //          .attr('class','pie-btn-use')
      
      // this.initPie(industryNodeG)
      
      // 
      let bgShade = d3.select('.nodes')
        .append('rect')
        .attr('id','bg-shade')
        .style('fill','black')
        .style('width','100%')
        .style('height','100%')
        .style('display','none');

      this.bgShade = bgShade;

        nodeG.call(
          d3
            .drag()
            .on("start", (d)=>vm.dragstarted(d,simulation)) //
            .on("drag", (d)=>vm.dragged(d,vm.width,vm.height)) //
            .on("end", (d)=>vm.dragended(d,simulation)) //
        );

      return {nodeG,industryNodeG,mainNodeG,industryPie,pieBtnUse,addBtnUse,};
    },
    //  
    initMainBtn(mainNodeG,outerRadius=130,borderRadius=30){
                          //
      let 
      // 
      colorList =['#61a126','#8bd126','#b1e485','#37c2e2','#01add3','#0182a2','#e5a21a','#205066','#466ac6','#f43c3c','#d33131','#a02d65','#e83d9f','#045a4d','#18837b','#1bb4a2'], //
      // 
      datas = [
        {title:'',value:1},
        {title:'',value:1},
        {title:'',value:1},
        {title:'',value:1},
        {title:'',value:1},
        {title:'',value:1},
        {title:'',value:1},
        {title:'',value:1},
        {title:'',value:1},
        {title:'',value:1},
      ];
      // 
      // 'hiddenHalf' 
      datas.push(
        {title:'+',value:1},
        {title:'hiddenHalf',value:datas.length+1}
      )

      let
      fontSize = outerRadius/13, //

      // 
      circleR = innerRadius+fontSize*1.8,

      // 
      innerArcBaseR = 0,
      innerArcBesideR = outerRadius/2,

      // 
      outerArcBaseR = outerRadius,
      outerArcBesideR = outerRadius + borderRadius ,

      // 
      innerTextArcBaseR = circleR,
      innerTextArcBesideR = outerArcBaseR,

      // 
      outerIconArcBaseR = outerArcBaseR,
      outerIconArcBesideR = outerArcBesideR

      ;

      let
      // outerRadius = this.outerRadius, // 
      pie = d3.pie().value(item=>item.value), //
      pieRes = pie(datas), //
      innerRadius = outerRadius/2, //
      arc = d3.arc().innerRadius(0).outerRadius(outerRadius), //
      borderArc = d3.arc().innerRadius(outerRadius).outerRadius(outerRadius+borderRadius), //
      borderIconArc = d3.arc().innerRadius(outerRadius+borderRadius/2).outerRadius(outerRadius+borderRadius), //
      labelArc = d3.arc().innerRadius(innerRadius+fontSize*1.8).outerRadius(outerRadius), //
      piePathG  = mainNodeG.append('g').attr('id','path-g') // 
      // pieTextG  = mainNodeG.append('g').attr('id','text-g') // 
      ;

      pieRes.forEach((pieData,index)=>{
        
        let arcRes = arc(pieData), //
            borderRes = borderArc(pieData), //
            centerPoint = labelArc.centroid(pieData); // 
        let iconAng = JSON.parse(JSON.stringify(pieData))
        let centerBorder = borderIconArc.centroid(iconAng) // 
        ;
        console.log(centerBorder,pieData)
        let pathItem = piePathG.append('g') // 
                               .attr('class',`path-item-${index}`)
                               .attr('data-main-item',index)
            
            //  
            pathItem.append('path')
                    .attr('d',arcRes)
                    // .attr('fill',colorList[index])
                    .attr('fill',colorList[index])
                    .attr('data-main-item',index)
                    .style('stroke','black')
                    .style('stroke-width','1.3')
            
            // 
            pathItem.append('path')
                    .attr('d',borderRes)
                    .attr('fill','#3a465e')
                    .attr('data-main-item',index)
                    .style('stroke','black')
                    .style('stroke-width','1.3')
            // 
            pathItem.append('text')
                    .html(function(d){
                      return pieData.data.title
                    })
                    .attr('data-main-item',index)
                    .style('fill','white')
                    .style('font-size',fontSize)
                    .attr('text-anchor','middle') //
                    .attr('dominant-baseline','middle') //
                    .attr('transform',`translate(${centerPoint[0]},${centerPoint[1]}) rotate(${ (pieData.startAngle + pieData.endAngle)/2 * 180/Math.PI + 90})`)
                    // .attr('transform',`scale(${(outerRadius/2)/fontSize/4})`)
            
            // 
            pathItem.append('use')
                    .attr('href','#main-btn-icon')
                    .attr('transform',`translate(${centerBorder[0]},${centerBorder[1]}) rotate(${(pieData.startAngle + pieData.endAngle)/2 * 180/Math.PI })`)
                    .attr('width',borderRadius/2)
                    .attr('height',borderRadius/2)
                    .attr('fill','white')

      });
      /*
        // d3.selectAll('.main-path-item').on('click',(d)=>{
        //   alert('ok');
        //   console.log(d3.event)
        //   console.log(d)
        //   return
        // })
      */
      //  
      let lianCircle = mainNodeG.append('g')
                               .attr('id','lian-circle')
      // 
      lianCircle.append('circle')
                .attr('cx',0)
                .attr('cy',0)
                .attr('r',innerRadius*1.2)
                .style('fill','black') 

      //  (+)
      lianCircle.append('circle')
                .attr('cx',-(innerRadius*1.1)/2)
                .attr('cy',0)
                .attr('r',(innerRadius*1.1)/2.2)
                .style('fill','#f5aa14') 

      lianCircle.append('text')
                .html('+')
                .style('font-size',innerRadius/4)
                .attr('text-anchor','middle')
                .attr('dominant-baseline','middle') //
                .style('fill','white')
                .attr('transform',function(){
                  // console.log(`translate(${-(innerRadius*1.2)/2},0) rotate(270)`)
                  return `translate(${-(innerRadius*1.1)/2},0) rotate(270)`
                })
      mainNodeG.datum(function(d){
        let botHeight = d.radius/3; //
        vm.blackSideHeight = botHeight; //
        return d
      })  
      vm.mainBtnDiameter = outerArcBesideR*2*1.4 //
      vm.leftWidth = (vm.svgWidth - vm.mainBtnDiameter) / 2 //
      vm.rightWidth = vm.leftWidth //
      return mainNodeG;
    },
    
    //   
    initPieNode(industryPie){
      let 
      colorList =['#61a126','#8bd126','#b1e485','#37c2e2','#01add3','#0182a2','#e5a21a','#205066','#466ac6','#f43c3c','#d33131','#a02d65','#e83d9f','#045a4d','#18837b','#1bb4a2'], //
      datas = [],
      outerRadius = this.pieNodeRadius
      ;

      let
      //    innerArcBaseR0 
      innerArcBaseR = 0, //
      innerArcBesideR = outerRadius - 2,   //
      innerWidth = innerArcBesideR - innerArcBaseR, //

      // 
      outerArcBaseR = innerArcBesideR , //   
      outerArcBesideR = outerArcBaseR + innerArcBesideR/5, //
      outerWidth = outerArcBesideR - outerArcBaseR, //

      // 
      iconCircleR =innerWidth/1.55, //
      iconCircleBorder = 1.55, //

      //   
      numArcBaseR = iconCircleR,
      numArcBesideR = innerArcBesideR,
      numArcWidth = numArcBesideR - numArcBaseR, // 
      numFontSize = numArcWidth/5,

      //   
      provNameArcBaseR = outerArcBaseR,
      provNameArcBesideR = outerArcBesideR,
      provNameArcWidth = provNameArcBesideR - provNameArcBaseR, 
      provNameFontSize = provNameArcWidth/4

      ;

      // # 
      //   path 
      let creatAngle = d3.pie().value(item=>item.Count);//

      let innerArc = d3.arc().innerRadius(innerArcBaseR).outerRadius(innerArcBesideR); //
      let outerArc = d3.arc().innerRadius(outerArcBaseR).outerRadius(outerArcBesideR); //

      // let numArc = d3.arc().innerRadius(outerRadius/2 + outerRadius/6).outerRadius(outerRadius - 2); //
      // let textArc = d3.arc().innerRadius(outerRadius).outerRadius(outerRadius - 2+outerRadius/5); //

      let numArc = d3.arc().innerRadius(numArcBaseR).outerRadius(numArcBesideR); //
      let textArc = d3.arc().innerRadius(provNameArcBaseR + provNameArcWidth/5 ).outerRadius(provNameArcBesideR); //

      industryPie.datum((d)=>{
        // d3 
        datas.push(d.provinceList)
        d.radius = innerWidth + outerWidth // 
        return d
      })

      // let innerR = outerRadius - 2;
      // let outerR = (outerRadius - 2) + (outerRadius/2 - outerRadius - 3);
      // pieG = industryPie
      //path
      let provinceG = industryPie.selectAll('g')
          // province pathprovince
          // class:province
          // !----------- data------provinceListprivince
          .data(function(d){
            // dd.province 
            let {dataType,industryID,industryName,nodeOrder,pieType} = d
            let arr = []
            creatAngle(d.provinceList).forEach((ele,index)=>{
              arr.push({
                dataType,industryID,industryName,nodeOrder,pieType,
                innerArc:innerArc(ele), // path
                outerArc:outerArc(ele), // path
                provinceName:ele.data.Name,//
                provinceCount:ele.data.Count,//
                provinceID:ele.data.ID,//ID
                textPosition:textArc.centroid(ele), // 
                numPosition:numArc.centroid(ele), // 
                angle:ele//
              })
            })
            return arr
          })
          .enter()
          .append('g')
          .attr('class','province')

      // 
      let provinceInnerPath = provinceG.append('path')
                                  .attr('d',function(d){
                                    return d.innerArc
                                  })
                                  .style('stroke','black')
                                  .style('stroke-width','1.3')
                                  .style('fill',function(d,index){
                                    if(index < colorList.length){
                                      return colorList[index]
                                    }else{
                                      return colorList[0]
                                    }
                                  })

      // 
      let provinceOuterPath = provinceG.append('path')
                                       .attr('d',function(d){
                                         return d.outerArc
                                       })
                                       .style('stroke','black')
                                       .style('stroke-width','1.3')
                                       .style('fill',function(d,index){
                                         if(index < colorList.length){
                                           return colorList[index]
                                         }else{
                                           return colorList[0]
                                         }
                                       })
                                       .style('fill','#3a465e')

      // 
      let provinceName =  provinceG.append('text')
                  .attr('text-anchor','middle') //
                  .attr('dominant-baseline','middle') //
                  .html(function(d){
                    return d.provinceName
                  })
                  .style('font-size',function(d){
                    return provNameArcWidth / (d.provinceName.length+1)
                    // return provNameFontSize
                  })
                  .style('fill','white')
                  .style('font-family','TRENDS')
                  .attr('transform',function(d){
                    let start = d.angle.startAngle
                    let end = d.angle.endAngle
                    let ang = ( start + end ) / 2
                    return `translate(${d.textPosition[0]},${d.textPosition[1]}) rotate(${ ang * 180 / Math.PI })`
                  })

      // 
      let provinceCount = provinceG.append('text')
                                    .attr('text-anchor','middle') //
                                    .attr('dominant-baseline','middle') //
                                    .html(function(d){
                                      let showCount = d.provinceCount
                                      showCount = (showCount/10000).toFixed(2)
                                      // if(showCount)
                                      d.showCount = showCount+''
                                      return d.showCount
                                    })
                                    .style('font-size',function(d){
                                      
                                      return numArcWidth / (d.showCount.length+1)
                                      // return numFontSize
                                    })
                                    .attr('fill','white')
                                    .style('font-family','TRENDS')
                                    .attr('transform',function(d){
                                      let start = d.angle.startAngle
                                      let end = d.angle.endAngle
                                      let ang = ( start + end ) / 2
                                      return `translate(${d.numPosition[0]},${d.numPosition[1]}) rotate(${ ang * 180 / Math.PI +90})`
                                    })

      // 
      let centerCircleG = industryPie.append('g')
      let centerCircle  = centerCircleG.append('circle')
                                    .attr('r',iconCircleR)
                                    .style('fill',function(d){
                                      if(d.dataType == 'down'){
                                        return 'black'
                                      }
                                      else if(d.dataType == 'up'){
                                        return 'white'
                                      }
                                    })
                                    .style('stroke','black')
                                    .style('stroke-width',iconCircleBorder)
          centerCircleG.append('text')
                     .html(function(d){
                       if(d.dataType == 'down'){
                         return ''
                       }
                       else if(d.dataType == 'up'){
                         return ''
                       }
                     })
                     .style('fill',function(d){
                       if(d.dataType == 'down'){
                         return 'white'
                       }else if(d.dataType == 'up'){
                        return 'black'
                       }
                     })
                     .style('font-size',innerWidth/2.5)
                     .style('font-family','TRENDS')
                     .attr('text-anchor','middle') //
                     .attr('dominant-baseline','middle') //
                     .attr('transform','translate(0,2)')
        return industryPie;
    },
    // 
    initBotInfoPie(){
      
      let botHeight = this.blackSideHeight, //  
          leftWidth = this.leftWidth, // 
          rightWidth = this.rightWidth, //
          mainBtnDiameter = this.mainBtnDiameter, //
          svgHeight = this.svgHeight,
          svgWidth = this.svgWidth,
          pieX={
            left1:leftWidth/4,
            left2:leftWidth*3/(4+0.2),
            right1:svgWidth - rightWidth + rightWidth * 1/(4-0.6),
            right2:svgWidth - rightWidth + rightWidth * 3/4
          },
          pieY = svgHeight - botHeight // Y
      ;

      let datas = [
        {title:'showHalf',num:1},
        {title:'hiddenHalf',num:1}
      ]
      

      // Arc 
      let creatAngle = d3.pie().value(item=>item.num);//

      let infoArc = d3.arc().innerRadius(0).outerRadius(leftWidth/(4+1)); //
      
      function infoPieFactory(node,colorList){
        creatAngle(datas).forEach((ele,index)=>{
          console.log(ele);
          node.append('path')
              .attr('d',function(){
                return infoArc(ele)
              })
              .style('fill',colorList[index])
        })
        node.append('circle')
            .attr('r',(leftWidth/(4+1))/1.5)
      }
      

      let svg = d3.select('svg')
      let infoPieG = svg.append('g')
                        .attr('class','info-pie-g')
                        
                        
      // 
      let  recommendedPieG = infoPieG.append('g')
                                    .attr('class','recommended-pieG')
                                    .attr('transform',`translate(${pieX.left1},${pieY})`)

      let recPie = recommendedPieG.append('path')
                                 .attr('class','rec-pie')
      infoPieFactory(recommendedPieG,['#e5a21a','#3a465e'])

      // 
      let dataDegreePieG = infoPieG.append('g')
                                  .attr('class','data-degree-pieG')
                                  .attr('transform',`translate(${pieX.left2},${pieY})`)
        let dataPie = dataDegreePieG.append('path')
                                        .attr('class','data-pie')
        infoPieFactory(dataDegreePieG,['#0182a2','#3a465e'])

      // 
      let visitNumPieG = infoPieG.append('g')
                                .attr('class','visit-num-pieG')
                                .attr('transform',`translate(${pieX.right1},${pieY})`)
      let visPie = visitNumPieG.append('path')
                               .attr('class','vis-pie')
        infoPieFactory(visitNumPieG,['#f43c3c','#3a465e'])

      // 
      let isAuthPieG = infoPieG.append('g')
                               .attr('class','is-auth-pieG')
                               .attr('transform',`translate(${pieX.right2},${pieY})`)
        let isAuthPie = isAuthPieG.append('path')
                               .attr('class','is-auth-pie')
        infoPieFactory(isAuthPieG,['#ff8a00','#3a465e'])
    },
    // 
    initBotInfoSide(){
      
      let botHeight = this.blackSideHeight, //  
          leftWidth = this.leftWidth, // 
          rightWidth = this.rightWidth, //
          mainBtnDiameter = this.mainBtnDiameter, //
          svgHeight = this.svgHeight,
          svgWidth = this.svgWidth
      ;

      let svg = d3.select('svg')
      
      // 
        let botSideG = svg.append('g')
                          .attr('class','botSide')
                          .attr('transform',`translate(0,${svgHeight - botHeight})`)
        let botSide = botSideG.append('rect')
                              // .attr('x',0)
                              // .attr('y',svgHeight - botHeight)
                              .attr('width',svgWidth)
                              .attr('height',botHeight)
      let companyName = botSideG.append('text')
                               .html('')
                               .style('fill','white')
                               .style('font-size',botHeight/1.5)
                               .attr('text-anchor','middle') //
                               .attr('dominant-baseline','middle') //
                               .attr('transform',function(d){
                                //  svgHeight - botHeight/2
                                 return `translate(${svgWidth/2},${botHeight/2})`
                               })
                              //  .attr('x',svgWidth/2)
                              //  .attr('y',svgHeight - botHeight/2)
      let recommendedNum = botSideG.append('text')
                                   .html('')
                                   .style('fill','white')
                                   .style('font-size',botHeight/2)
                                   .attr('text-anchor','middle') //
                                   .attr('dominant-baseline','middle') //
                                   .attr('transform',function(d){
                                     //  svgHeight - botHeight/2
                                     return `translate(${leftWidth/4},${botHeight/2})`
                                   })
          // 
          let dataDegree = botSideG.append('text')
                                   .html('')
                                   .style('fill','white')
                                   .style('font-size',botHeight/2)
                                   .attr('text-anchor','middle') //
                                   .attr('dominant-baseline','middle') //
                                   .attr('transform',function(d){
                                     //  svgHeight - botHeight/2
                                     return `translate(${leftWidth * 3/(4+0.2)},${botHeight/2})`
                                   })
          // 
          let visitNum = botSideG.append('text')
                                   .html('')
                                   .style('fill','white')
                                   .style('font-size',botHeight/2)
                                   .attr('text-anchor','middle') //
                                   .attr('dominant-baseline','middle') //
                                   .attr('transform',function(d){
                                     //  svgHeight - botHeight/2
                                     return `translate(${svgWidth - rightWidth + rightWidth * 1/(4-0.6)},${botHeight/2})`
                                   }) 
          let isAuth = botSideG.append('text')
                                   .html('')
                                   .style('fill','white')
                                   .style('font-size',botHeight/2)
                                   .attr('text-anchor','middle') //
                                   .attr('dominant-baseline','middle') //
                                   .attr('transform',function(d){
                                     //  svgHeight - botHeight/2
                                     return `translate(${svgWidth - rightWidth + rightWidth * 3/4 },${botHeight/2})`
                                   }) 
    },

    // link  node  xy
    ticked(link,industryNodeG,mainNodeG,width,height) {
      // width  height  svg
      link
        .attr("x1", function(d) {
          return d.source.x;
        })
        .attr("y1", function(d) {
          return d.source.y;
        })
        .attr("x2", function(d) {
          return d.target.x;
        })
        .attr("y2", function(d) {
          return d.target.y;
        });

      // 
      industryNodeG
        .attr('transform',function(d){
          
          if(d.pieType == 'main'){
            // 
            d.x = vm.svgWidth/2;
            d.y = vm.svgHeight
            // rotate(90) 
            let botHeight = d.radius/3; // 
            return `translate(${d.x},${d.y - botHeight}) rotate(90) scale(${vm.mainBtnscale})`
          }

          //
          if( d.x <= d.radius){
            d.x = d.radius
          }
          if( d.x > width - d.radius){
            d.x = width - d.radius
          }
          if( d.y <= d.radius){
            d.y = d.radius
          }
          if( d.y > height - d.radius){
            d.y = height - d.radius
          }

          return `translate(${d.x},${d.y})`
        });

      //  
      mainNodeG.attr('transform',function(d){
        d.x = vm.svgWidth/2;
        d.y = vm.svgHeight
        // rotate(90) 
        let botHeight = vm.blackSideHeight; //
        // vm.blackSideHeight = botHeight; //
        return `translate(${d.x},${d.y - botHeight}) rotate(90) scale(1.5)`
      })

    },
    
    // 
    eventManage(nodeList,simulation,graphData,link,industryNodeG,mainNodeG,svgWidth,svgHeight){
      // d3
      
      let svg = d3.select('svg')
      svg.on('click',function(){
        let Target = d3.event.target;

        // DOM 
        switch(Target){ 
          case vm.bgShade.node():
            vm.isPlus = false;
            vm.canDragged = true;
            vm.shade = false;
            vm.bgShade.style('display','none');
            vm.bgShade.raise();
            simulation.restart();

          break;
          default:
            console.dir(Target);
            console.log(this)
        }
        // targetd3  s
        if(!Target.__data__) return
        if(Target.__data__.nodeOrder) {
          //className()
          let plusItem = d3.select('#'+Target.__data__.nodeOrder),
          pieType = Target.__data__.pieType //
          ;
          //
          if(!vm.isPlus) { 
            vm.isPlus = true;
            vm.nodePlus(plusItem,simulation); //
            return;
          }else{
            // 
            switch(pieType){
              case 'main':
                //  
                let btnOrder = Target.dataset.mainItem
                vm.mainBtnItem(btnOrder) // btnOrder

              break;
              case 'pie':
                
              break;
              case 'add':

              break;

            }
          }
          

        }
        // targetd3  e



      });

      // link nodex,y
      simulation.nodes(graphData.nodes).on("tick", ()=>vm.ticked(link,industryNodeG,mainNodeG,svgWidth,svgHeight));
      
    },

    // 
    mainBtnItem(order){
      //order  

      let orderBtn = d3.select(`.path-item-${order}`)
      orderBtn.attr('transform','scale(1.2)')
      setTimeout(()=>{
        orderBtn.attr('transform','scale(1)')
      },1000)

      switch(order){
        case '0':
        console.log('0')
        break;
        case '9':
        console.log('9')
        break;
      }

    },

    // 
    nodePlus(plusItem,simulation){
      simulation.stop()
      plusItem.raise() //   
          .attr('transform',function(d){
            // (/2)[]/ == [] 0.4 
            let scaleX = (vm.baseSide/2.4) / d.radius
            return `translate(${vm.svgWidth/2},${vm.svgHeight/2}) scale(${scaleX})`
          })
      simulation.stop()
      
      //  
      this.canDragged = false;
      // 
      this.bgShade.style('display','block');

    },

    // node  
    dragstarted(d,simulation) {
      if(d.pieType=='main') return
      if(!this.canDragged) return;
      //d3.event.active   restart()
      if (!d3.event.active) simulation.alphaTarget(0.1).restart();
      d.fx = d.x;
      d.fy = d.y;
    },
    // 
    dragged(d,width,height) {
      if(d.pieType=='main') return
      // 
      if(d3.event.x <= 0 || d3.event.y <= 0 || d3.event.x >= width || d3.event.y >= height ) return;
        d.fx = d3.event.x;
        d.fy = d3.event.y;
    },
    // 
    dragended(d,simulation) {
      if (!d3.event.active) simulation.alphaTarget(0);
      d.fx = null;
      d.fy = null;
    },
    
  },
  watch:{
    
  }
};
</script>
